 회사의\n          허가를 받았을 경우에는 예외로 합니다.\n        ")]),_vm._v(" "),_c('p',{staticClass:"pt-5 subtitle-1"},[_vm._v("제 14 조 (이용계약의 종료 및 이용해지)")]),_vm._v(" "),_c('ul',{staticClass:"subtitle-1"},[_c('li',[_vm._v("\n            가. 고객이 회사가 운영하는 태문건설 홈페이지에서 제공하는 서비스의\n            제공을 받았을 시에 이용계약은 자동으로 종료됩니다.\n          ")]),_vm._v(" "),_c('li',[_vm._v("\n            나. 회사는 고객이 본 약관에 정해져 있는 고객으로써의 의무 불이행\n            및 기타 회사에 피해를 주는 등의 사유가 발생하였을 경우, 고객의\n            자격을 박탈하여 태문건설 이 제공하는 제반의 서비스를 사용할 수\n            없도록 조치할 수 있습니다.\n          ")]),_vm._v(" "),_c('li',[_vm._v("\n            다. 회사는 고객이 다음 각 호의 행위를 하였을 경우 당해 고객을\n            이용해지 할 수 있습니다.\n          ")]),_vm._v(" "),_c('ul',{staticClass:"subtitle-2"},[_c('li',[_vm._v("1) 공공질서 및 미풍양속에 반하는 경우")]),_vm._v(" "),_c('li',[_vm._v("2) 범죄 행위에 관련되는 경우")]),_vm._v(" "),_c('li',[_vm._v("\n              3) 국익이나 사회적 공익을 저해할 목적으로 서비스 이용을 계획\n              또는 실행할 경우\n            ")]),_vm._v(" "),_c('li',[_vm._v("4) 타인의 명예를 손상시키거나 불이익을 주는 경우")]),_vm._v(" "),_c('li',[_vm._v("5) 고객이 이용계약신청서에 허위 사실을 기재한 경우")]),_vm._v(" "),_c('li',[_vm._v("\n              6) 서비스에 위해를 가하는 등 서비스의 건전한 이용을 저해하는\n              경우\n            ")]),_vm._v(" "),_c('li',[_vm._v("\n              7) 고객이 이용제한규정을 위반하거나 그 이용제한 기간 내에\n              제한사유를 해소하지 않는 경우\n            ")]),_vm._v(" "),_c('li',[_vm._v("\n              8) 기타 관련법령이나 회사가 정한 이용조건에 위배되거나 본\n              약관에서 규정한 고객의 의무를 이행하지 아니하는 경우\n            ")])])]),_vm._v(" "),_c('p',{staticClass:"pt-5 subtitle-1"},[_vm._v("제 15 조 (고객 정보보호)")]),_vm._v(" "),_c('ul',{staticClass:"subtitle-1"},[_c('li',[_vm._v("\n            가. 회사는 서비스 이용을 위하여 고객이 제공한 개인정보를 관련 업무\n            이외의 목적으로 타인에게 누설 배포하거나 사용할 수 없습니다. 단,\n            서비스의 제공을 위하여 고객의 동의하에 회사의 협력사 또는 협력사와\n            관련하여 업무 제휴된 업체는 고객의 개인정보를 회사와 함께 공유할\n            수 있습니다.\n          ")]),_vm._v(" "),_c('li',[_vm._v("\n            나. 회사가 서비스의 제공을 위하여 고객의 동의하에 회사의 제휴사\n            또는 제휴사와 서비스의 제공과 관련하여 업무 제휴된 업체에 고객이\n            제공한 개인정보를 제공할 시에는 최초